import { getProfiles } from "@/api/profile";
import { getArticles } from "@/api/article";
import { mapState } from "vuex";
import FollowBtn from "../components/FollowBtn.vue";
import ArticleCard from "../components/ArticleCard.vue";
const limit = 20;

export default {
  components: { ArticleCard, FollowBtn },
  name: "profile",
  middleware: "authenticated",
  watchQuery: ["tab", "page"],
  head() {
    FollowBtn;
    return {
      title: `${this.profile.username}-condiut`,
      meta: [
        {
          hid: "description",
          name: "description",
          content: this.profile.bio,
        },
      ],
    };
  },
  async asyncData({ params, query }) {
    const page = Number(query.page || 1);
    const offset = (page - 1) * limit;
    const tab =
      query.tab === "favorited_articles" ? "favorited_articles" : "my_articles";
    let [
      {
        data: { articles, articlesCount },
      },
      {
        data: { profile },
      },
    ] = await Promise.all([
      getArticles(
        tab === "my_articles"
          ? {
              page,
              offset,
              limit,
              author: params.username,
            }
          : {
              page,
              offset,
              limit,
              favorited: params.username,
            }
      ),
      getProfiles(params.username),
    ]);
    return {
      page,
      tab,
      profile,
      articles,
      articlesCount,
    };
  },
  computed: {
    ...mapState(["user"]),
    totalPage() {
      return Math.ceil(this.articlesCount / limit);
    },
  },
};
